import React, { lazy } from "react";
import { Route, Switch, withRouter } from "react-router-dom";
import EsperarComponente from "./shared/layout/EsperarComponente";


const TesteAleatoriedade = lazy(() =>
  import("./features/testeAleatoriedade")
);

export const Routes = () => {
  let rotas = (
    <Switch>
      <Route exact
        path="/how-random-you-are.github.io/"
        component={EsperarComponente(TesteAleatoriedade)}
      />
    </Switch>
  );
  const AppRoutes = withRouter((p) => rotas);
  return <AppRoutes />;
};

export default Routes;