
// Types

export const NOTIFICAR = 'notificacao/NOTIFICAR';
export const NOTIFICAR_SUCESSO = 'notificacao/NOTIFICAR_SUCESSO';
export const OCULTAR_NOTIFICACAO = 'notificacao/OCULTAR_NOTIFICACAO';

// Reducer

const INITIAL_STATE = {
  mensagem: '',
  tipo: 'erro',
  exibeNotificacao: false
};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {

    case NOTIFICAR:
      return {
        ...state,
        mensagem: action.payload.mensagem,
        tipo: 'erro',
        exibeNotificacao: true
      };

    case NOTIFICAR_SUCESSO:
      return {
        ...state,
        mensagem: action.payload.mensagem,
        tipo: 'sucesso',
        exibeNotificacao: true
      };
      
    case OCULTAR_NOTIFICACAO:
      return {
        ...state,
        mensagem: '',
        exibeNotificacao: false
      };

    default:
      return state;
  }
};

// Action creators

export const exibirNotificacao = (mensagem) => {
  return {
    type: NOTIFICAR,
    payload: { mensagem }
  };
};

export const exibirNotificacaoSucesso = (mensagem) => {
  return {
    type: NOTIFICAR_SUCESSO,
    payload: { mensagem }
  };
};

export const ocultarNotificacao = () => {
  return {
    type: OCULTAR_NOTIFICACAO
  };
};