// Constantes
export const PRIMARY_COLOR = "#656D75";
export const SECONDARY_COLOR = "#D31245";
export const LIGHT_GRAY1 = "#8CA0B3";
export const LIGHT_GRAY2 = "#C0C6D6";
export const LIGHT_GRAY3 = "#61688B";
export const BACKGROUND_COLOR = "#FFFFFF";
export const FONT_FAMILY = "Helvetica";

export const ESPACAMENTO_VERTICAL = "16px";

const SPACING = 8;

// Mixins

const generateNClasses = (name, number, calc) => {
  let classes = {};

  for (let i = 0; i <= number; i++) {
    let className = name.replace("{i}", i);
    classes[className] = calc(i);
  }

  return classes;
};

const spacing = (num) => (num ? num * SPACING + "px" : "0");

// Tema

export default {
  palette: {
    primary: {
      light: PRIMARY_COLOR,
      main: PRIMARY_COLOR,
      dark: PRIMARY_COLOR,
    },
    secondary: {
      light: SECONDARY_COLOR,
      main: SECONDARY_COLOR,
    },
    action: {
      disabled: {},
    },
    background: {
      default: '#ffffff'
    }
  },

  spacing: SPACING,

  typography: {
    fontFamily: FONT_FAMILY,

    h1: {
      fontWeight: "bold",
      fontSize: "2.25rem",
      color: PRIMARY_COLOR,
    },
    h2: {
      fontWeight: "600",
      fontSize: "1.5rem",
      color: PRIMARY_COLOR,
    },
    h3: {
      fontWeight: "600",
      fontSize: "1.3125rem",
      color: PRIMARY_COLOR,
    },
    h4: {
      fontWeight: "500",
      fontSize: "1rem",
      color: PRIMARY_COLOR,
    },

    body1: {
      fontWeight: "500",
      fontSize: "0.75rem",
    },

    body2: {
      fontWeight: "lighter",
      fontSize: "0.625rem",
    },

    subtitle1: {
      fontWeight: "600",
      fontSize: "1rem",
      color: LIGHT_GRAY2,
    },

    subtitle2: {
      fontWeight: "normal",
      fontSize: "0.75rem",
      color: LIGHT_GRAY1,
    },
  },

  overrides: {
    MuiOutlinedInput: {
      root: {
        "&.Mui-disabled": {
          backgroundColor: "rgba(192,192,222,0.15)",
        },
        color: PRIMARY_COLOR,
        fontWeight: "500",
        fontSize: "1rem",
      },
    },
    MuiFormLabel: {
      root: {
        fontFamily: FONT_FAMILY,
        color: "#636D76",
        fontSize: "1rem",
        fontWeight: "500"
      },
    },
    MuiButton: {
      root: {
        textTransform: "none",
        fontWeight: "600",
        "&.Mui-disabled": {
          backgroundColor: LIGHT_GRAY2,
          color: "#ffffff",
        }
      }
    },
    MuiTableCell: {
      root: {
        '&.spacing-1': {
          padding: '14px 8px 14px 8px'
        },
      },
      head: {
        fontSize: '14px',
        color: LIGHT_GRAY3,
        fontWeight: 'normal',
        padding: `10px ${spacing(1)} 2px ${spacing(1)}`
      },
      footer: {
        borderBottom: '0px'
      },
      body: {
        fontSize: '16px',
        fontWeight: '500',
        color: PRIMARY_COLOR,
        padding: `10px ${spacing(1)} 10px ${spacing(1)}`,
        borderBottom: '1px solid #F9F9F9'
      }
    },
    MuiFormHelperText: {
      contained: {
        marginLeft: "0px"
      }
    },
    MuiCheckbox: {
      root: {
        "&.Mui-disabled": {
          color: LIGHT_GRAY2
        }
      }
    },
    MuiTabs: {
      root: {
        fontFamily: FONT_FAMILY
      }
    },
    MuiTab: {
      root: {
        "&.Mui-selected": {
          color: SECONDARY_COLOR
        },
        fontWeight: "600",
        fontSize: "1rem",
      }
    },
    MuiButtonBase: {
      root: {
        textTransform: "none !important",
        minWidth: "0px !important"
      }
    }
  },

  global: {
    ".text-overflow-ellipsis": {
      textOverflow: "ellipsis",
      overflow: "hidden",
      whiteSpace: "nowrap",
    },
    ".italic": {
      fontStyle: "italic",
    },
    ".bold": {
      fontWeight: "bold",
    },
    ".bolaVermelha": {
      borderRadius: "50%",
      height: "5px",
      width: "5px",
      backgroundColor: SECONDARY_COLOR,
      display: "inline-block",
      marginBottom: "4px",
      marginRight: "12px"
    },
    ".body3": {
      fontWeight: "lighter",
      lineHeight: "1.4"
    },
    ...generateNClasses(".mb-{i}", 8, (i) => ({
      marginBottom: spacing(i * 0.5),
    })),
    ...generateNClasses(".mt-{i}", 8, (i) => ({ marginTop: spacing(i * 0.5) })),
    ...generateNClasses(".mr-{i}", 8, (i) => ({
      marginRight: spacing(i * 0.5),
    })),
    ...generateNClasses(".ml-{i}", 8, (i) => ({
      marginLeft: spacing(i * 0.5),
    })),
    ...generateNClasses(".pl-{i}", 8, (i) => ({
      paddingLeft: spacing(i * 0.5),
    })),
    ...generateNClasses(".pr-{i}", 8, (i) => ({
      paddingRight: spacing(i * 0.5),
    })),
    ...generateNClasses(".pt-{i}", 8, (i) => ({
      paddingTop: spacing(i * 0.5),
    }))
  },
};
