// Types
export const INSERIR_DADOS_ACESSO = 'app/login/INSERIR_DADOS_ACESSO';
export const LIMPAR_DADOS_ACESSO = 'app/login/LIMPAR_DADOS_ACESSO';

// Reducer
export const INITIAL_STATE = {
  usuario: null,
  acessoNegado: false
};
  
export default function reducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case INSERIR_DADOS_ACESSO:
      return {
        ...state,
        usuario: action.payload,
        tempoRenovarSecao: null,
        acessoNegado: false
      };
    case LIMPAR_DADOS_ACESSO:
      return { ...INITIAL_STATE, acessoNegado: true, usuario: null };
    default:
      return state;
  }
}

// Action creators
export const atualizarDadosAcesso = usuario => {
  return {
    type: INSERIR_DADOS_ACESSO,
    payload: usuario
  };
};

export const limparDadosAcesso = () => {
  return {
    type: LIMPAR_DADOS_ACESSO
  };
};